import { formatDate } from '@angular/common';
import { Inject, Injectable, LOCALE_ID } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class RxDateUtilsService {
  constructor(@Inject(LOCALE_ID) private locale: string) {}

  formatDate(date: string | number | Date, format: string, locale?: string): string {
    return formatDate(date, format, locale ?? this.locale);
  }

  isTwelveHourClock(): boolean {
    const dateString = new Date().toLocaleString();

    return /am|pm/i.test(dateString);
  }
}
